import React, { createElement } from 'react'
import { useMeasure } from 'react-use';

import Box from './Box'
import Text from './Text'
import { responsive } from './ThemeProvider/theme';

const LeftTitle = ({ title, description, smallTilte, descPt, titleSpacing, big, note, multiple}) => {
  const [ref, { width }] = useMeasure();
  return (
    <>
      {big ? <Text fontSize="1.125rem" letterSpacing={responsive('1px', '3px')} fontWeight={responsive('700', '500')}>{smallTilte}</Text> : <Text.H4>{smallTilte}</Text.H4>}
      <Box.Inline ref={ref}>
        {createElement(big ? Text.H1 : Text.H2,
          {
            letterSpacing: titleSpacing || '2px'
          },
          title
        )}
      </Box.Inline>
      <Box textAlign="left" fontSize={responsive('0.875em', '1em')} pt={descPt || '0.625rem'} width={responsive('auto', width * (multiple || 1))} whiteSpace="pre-wrap">
        {description}
      </Box>
      {note &&
        <Box textAlign="left" color="noteColor" pt={descPt || '0.625rem'} width={responsive('auto', width * (multiple || 1))} whiteSpace="pre-wrap">
          {note}
        </Box>
      }
    </>
  )
}

export default LeftTitle
